/* nunito-200 - latin */
@font-face {
    font-family: 'Nunito';
    font-style: normal;
    font-weight: 200;
    src: url('./nunito-v12-latin-200.eot');
    /* IE9 Compat Modes */
    src: local('Nunito ExtraLight'), local('Nunito-ExtraLight'),
        url('./nunito-v12-latin-200.eot?#iefix') format('embedded-opentype'),
        /* IE6-IE8 */
        url('./nunito-v12-latin-200.woff2') format('woff2'),
        /* Super Modern Browsers */
        url('./nunito-v12-latin-200.woff') format('woff'),
        /* Modern Browsers */
        url('./nunito-v12-latin-200.ttf') format('truetype'),
        /* Safari, Android, iOS */
        url('./nunito-v12-latin-200.svg#Nunito') format('svg');
    /* Legacy iOS */
}

